export class VueGMDSS {
  constructor () {
    // 自动模式
    // 频率列表
    /*
    * {
      frequenc 频率
      num 通道
    }
    */
    this.freqList = []
    // 保存的当前频率
    this.receingNum = ''
    // 是否打印
    this.print = false
    // 当地时间
    this.time = new Date()
    this.times = new Date()
    // 是否开始定时
    this.itemType = false
    // prog模式下设置的数据合集
    /*
    {
      chaNum 通道号
      time1Text 时间1
      time2Text 时间2
      fchNum fch值
      manu 接收模式
      half HALFS按钮的值
    }
    */
    // 调亮度变量
    this.lightness = 10
    // 开机状态
    this.power = false
    // 自动模式下保存的值
    this.progAutoList = []
    // 手动模式下保存的值
    this.progManuList = []
    // ioc扫描密度合集
    this.iocList = ['288', '576']
    // scan 扫描速度合集
    this.scanList = ['60', '90', '120', '240']
    // 电源定时开关状态
    this.timerType = false
    // 是否打印完成状态
    this.dbprint = false
    // top值
    this.paperNum = -400
    // 用户信息
    this.userinfo = {
      'mmsi': '',
      'imo': '',
      'callsign': '',
      'name': '',
      'phone': '',
      'email': '',
      'arqid5': '',
      'arqid9': '',
      'gfecid5': '',
      'gfecid9': '',
      'lat': 18.12,
      'lon': -20.20,
      'allowrun': '',
      'item': ''
    }
  }
  doTimer () {
    let time = this.time.getTime()
    time += 1000
    this.time = new Date(time)
    if (this.itemType === true) {
      if (this.times <= this.time) {
        this.print = true
        console.log('我被打印了')
        this.itemType = false
      }
      console.log(this.time)
    }
  }
}



